.footer{
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 50px;
    position:relative;
    top:200px;
}
.footer-logo{
    display:flex;
    align-items:center;
    margin-left:1.5em;
    gap: 20px;   
}

.footer-links{
    display:flex;
    list-style:none;
    gap: 50px;
    color: #252525;
    font-size: 20px;
}
.footer-links li{
    cursor:pointer;
}
.footer-social-icon{
    display:flex;
    gap:30px;
}
.footer-icons-container{
   padding: 10px;
   padding-bottom: 6px;
   font-size:xx-large;
}
.footer-icons-container:hover{
    transform:scale(1.1);
    transition:0.2s;
    cursor: pointer;
}

.footer-copyright{
    display:flex;
    flex-direction: column;
    align-items: center;
    gap:30px;
    width: 100%;
    margin-bottom: 30px;
    color: #1a1a1a;
    font-size: 20px;
}
.footer-copyright hr{
    width: 80%;
    border: none;
    border-radius: 10px;
    height: 3px;
    background: #c7c7c7;
}

@media(max-width:400px){
    .footer{
        gap: 20px;
        margin-left:-1em;
    }
    .footer-logo{
        gap:10px;
        align-items: end;
    }
    .footer-logo img{
        width:40px;
        margin-left:-0.5em;
    }
    .footer-logo p{
        font-size: 25px;
    }
    .footer-links{
        font-size: 14px;
        gap: 10px;
    }
    .footer-social-icon img{
         width: 22px;
    }
    .footer-copyright{
        font-size:13px;
    }
    .footer-copyright hr{
        margin-left:1em;
    }
}

@media (max-width:500px) and (min-width:400px){
    .footer{
        gap: 20px;
        margin-left:-1em;
    }
    .footer-logo{
        gap:10px;
        align-items: end;
    }
    .footer-logo img{
        width:40px;
        margin-left:-0.5em;
    }
    .footer-logo p{
        font-size: 25px;
    }
    .footer-links{
        font-size: 14px;
        gap: 10px;
    }
    .footer-social-icon img{
         width: 22px;
    }
    .footer-copyright{
        font-size:13px;
    }
    .footer-copyright hr{
        margin-left:1.5em;
    }
}